import { useState, useEffect } from 'react';
import './App.css';
import { initFlowbiteHelper } from './flowbiteHelper';
import 'flowbite';


function App() {

  initFlowbiteHelper()
  // dark mode toggle
  const [darkMode, setDarkMode] = useState(true);

  useEffect(() => {
    if (localStorage.theme === 'dark' || (!('theme' in localStorage) && window.matchMedia('(prefers-color-scheme: dark)').matches)) {
      document.documentElement.classList.add('dark')
      setDarkMode(true);
    } else {
      document.documentElement.classList.remove('dark')
      setDarkMode(false)
    }
  }, []);

  const toggleDarkMode = () => {
    darkMode ?
      document.documentElement.classList.remove('dark') :
      document.documentElement.classList.add('dark');

    setDarkMode(prevDarkMode => !prevDarkMode);
  };

  return (
    <div className="App flex p-lg">

      <div className="stack-v gap-lg w-full">

        {/* Dark mode toggle */}
        <div className="stack-h gap-md items-center justify-end">
          <label>Switch theme: </label>
          <button className="sm" onClick={toggleDarkMode}>{darkMode ? 'Light' : 'Dark'}</button>
        </div>

      <div className="stack-h gap-md" role="button"><button className="primary sm" data-title="tooltip" data-tooltip-target="generated-tooltip-1">primary</button><div id="generated-tooltip-1" role="tooltip" className="absolute z-10 invisible inline-block px-3 py-2 text-sm font-medium text-white transition-opacity duration-300 bg-gray-900 rounded-lg shadow-xs opacity-0 tooltip dark:bg-gray-700">
      tooltip
      <div className="tooltip-arrow" data-popper-arrow=""></div>
    </div><button className="secondary sm">secondary</button><button className="text sm">text</button><button className="warning icon-error sm">warning</button></div>


        <button data-tooltip-target="tooltip-default" type="button" className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Default tooltip</button>

      <div id="tooltip-default" role="tooltip" className="absolute z-10 invisible inline-block px-3 py-2 text-sm font-medium text-white transition-opacity duration-300 bg-gray-900 rounded-lg shadow-xs opacity-0 tooltip dark:bg-gray-700">
          Tooltip content
          <div className="tooltip-arrow" data-popper-arrow></div>
      </div>

        {
          ['', 'sm', 'lg'].map(size => (
            <div className="stack-v gap-md w-full items-center" key={`size-${size}`}>
              <label>Buttons {size ? `(${size})` : ''}</label>
              <div className="stack-h gap-md">
                <button title="tooltip" className={`primary ${size}`}>primary</button>
                <button className={`secondary ${size}`}>secondary</button>
                <button className={`text ${size}`}>text</button>
                <button className={`warning icon-error ${size}`}>warning</button>
              </div>
            </div>
          ))
        }

      </div>
    </div>
  );
}

export default App;
